.svg-icon {
  margin-bottom: -200px;
  position: absolute;
  top: 0;
  z-index: 1;
}

.nav-bar {
  background: $nav_bar_color;
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 50px;
  padding: 20px 0 20px 0;
  font-size: 20px;
  font-weight: 500;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 3;
  font-family: kalam-regular;
  font-size: 25px;
  span,
  a {
    padding: 10px 5px;
    color: $black_color;
    text-decoration: none;
    transition: 0.3s;
    cursor: pointer;
    &:hover {
      text-decoration: underline;
      transition: 0.5s;
    }
  }
  .logo {
    width: 130px;
    position: absolute;
    left: 10px;
    top: -20px;
  }
}

@media screen and (max-width: 850px) {
  .tech-slideshow > div {
    top: 60px;
  }
  .drop-down {
    display: flex;
    font-weight: 600;
    justify-content: center;
    line-height: 60px;
    background-color: $nav_bar_color;
    cursor: pointer;
    position: relative;
    z-index: 2;
    transition: 0.5s;
    &:hover {
      background-color: $service_table_color_brighter;
    }
  }
  .nav-bar {
    background: #faf4ff;
    flex-direction: column;
    gap: 5px;
    height: 100%;
    margin-bottom: -100px;
    transition: 0.3s;
    position: relative;
    text-align: center;
    letter-spacing: 2px;
    font-weight: 600;
    font-size: 18px;
  }
  .logo {
    display: none;
  }
  .contacts-link {
    margin-left: 5px;
  }
  .svg-icon {
    display: none;
  }
}

@media screen and (min-width: 850px) {
  .drop-down {
    display: none;
    left: -100px;
    position: absolute;
  }
  .nav-bar {
    margin-top: 0 !important;
  }
}

@media screen and (max-width: 1100px) and (min-width: 850px) {
  .nav-bar {
    gap: 20px;
  }
  .logo {
    display: none;
  }
  .svg-icon {
    display: none;
  }
}
@media screen and (min-width: 2500px) {
  // .tech-slideshow {
  //   top: 150px;
  // }
  .nav-bar {
    gap: 100px;
  }
  .logo {
    display: none;
  }
  .svg-icon {
    display: none;
  }
}
